import React, { Component } from 'react';
import dataQuran from '../../assets/quran-completed.json';
import Navbar from '../Navbar/Navbar';
import '../../css/Surah.css';
import '../../css/Murottal.css';
import Abdur from '../../assets/Abdur-Rahman as-Sudais.png';
import Mishari from '../../assets/Mishari Rashid al-`Afasy.jpg';
import Saad from '../../assets/Saad al-Ghamdi.png';



export default class AudioMurottal extends Component {

  constructor(props) {
    super(props)

    this.state = {
      data: dataQuran
    }
  }

  render() {
    const id = this.props.match.params.id;
    const dataQuran = this.state.data;
    let surahQuran = null;
    dataQuran.forEach(surah => {
      if (surah.name === id) {
        surahQuran = surah;
      }
    })

    let title =
      <div className="title-section">
        <div className="wrapping-title">
          <h3>{surahQuran.name} ({surahQuran.name_translations.ar})</h3>
          <label>{surahQuran.name_translations.id}</label>
        </div>
      </div>

    let pathURL = this.props.match.path;

    let audioMurottal = surahQuran.recitations;
    let pembaca = null;
    pembaca = audioMurottal.map((audio, index) => {
      let imgSource = audio.name.split("-")[0];
      let img = imgSource.split(" ")[0];
      if (img === "Mishari") {
        return (
          <div className="card-pembaca" key={index}>
            <div className="pictProfile">
              <img src={Mishari} alt={audio.name} />
            </div>
            <div className="audio">
              <label>{audio.name}</label>
              <audio controls className="audio-murottal">
                <source src={audio.audio_url} type="audio/mpeg" />
              </audio>
            </div>
          </div>
        )
      } else if (img === "Abdur") {
        return (
          <div className="card-pembaca" key={index}>
            <div className="pictProfile">
              <img src={Abdur} alt={audio.name} />
            </div>
            <div className="audio">
              <label>{audio.name}</label>
              <audio controls className="audio-murottal">
                <source src={audio.audio_url} type="audio/mpeg" />
              </audio>
            </div>
          </div>
        )
      } else if (img === "Saad") {
        return (
          <div className="card-pembaca" key={index}>
            <div className="pictProfile">
              <img src={Saad} alt={audio.name} />
            </div>
            <div className="audio">
              <label>{audio.name}</label>
              <audio controls className="audio-murottal">
                <source src={audio.audio_url} type="audio/mpeg" />
              </audio>
            </div>
          </div>
        )
      } else {
        return null;
      }
    })

    return (
      <div>
        <Navbar pathURL={pathURL} />
        {title}
        <div className="wrapping-murottal">
          {pembaca}
        </div>
      </div>
    )
  }
}
