import React, { Component } from 'react';
import { MenuItem } from './MenuItems';
import './Navbar.css';
import { Link } from 'react-router-dom';

class Navbar extends Component {

  state = {
    clicked: false
  }

  handleClickMenu = () => {
    this.setState({
      clicked: !this.state.clicked
    })
  }

  render() {
    const pathURL = this.props.pathURL;
    let nav = null;

    if (pathURL === '/MoslemApps/') {
      nav =
        <nav className="NavbarItems">
          <h1 className="navbar-logo">
            Al-Qur'an
            <i className="fas fa-quran"></i>
          </h1>
          <div className="menu-icon" onClick={this.handleClickMenu}>
            <i className={this.state.clicked ? 'fas fa-times' : 'fas fa-bars'}></i>
          </div>
          <ul className={this.state.clicked ? 'nav-menu active' : 'nav-menu'}>
            {MenuItem.map((item, index) => {
              return (
                <Link to={item.url} className={item.cName} key={index}>
                  <li>
                    {item.title}
                  </li>
                </Link>
              )
            })}
          </ul>
        </nav>
    } else if (pathURL === '/MoslemApps/Al-Qur-an/surah/:id') {
      nav =
        <nav className="NavbarItems">
          <h1 className="navbar-logo">
            <Link to="/MoslemApps/">
              <i className="fas fa-arrow-circle-left"></i>
            </Link>
            Al-Qur'an
              <i className="fas fa-quran"></i>
          </h1>
          <div className="menu-icon" onClick={this.handleClickMenu}>
            <i className={this.state.clicked ? 'fas fa-times' : 'fas fa-bars'}></i>
          </div>
          <ul className={this.state.clicked ? 'nav-menu active' : 'nav-menu'}>
            {MenuItem.map((item, index) => {
              return (
                <Link to={item.url} className={item.cName} key={index}>
                  <li>
                    {item.title}
                  </li>
                </Link>
              )
            })}
          </ul>
        </nav>
    } else if (pathURL === '/MoslemApps/Tafsir') {
      nav =
        <nav className="NavbarItems">
          <h1 className="navbar-logo">
            Tafsir
            <i className="fas fa-book-reader"></i>
          </h1>
          <div className="menu-icon" onClick={this.handleClickMenu}>
            <i className={this.state.clicked ? 'fas fa-times' : 'fas fa-bars'}></i>
          </div>
          <ul className={this.state.clicked ? 'nav-menu active' : 'nav-menu'}>
            {MenuItem.map((item, index) => {
              return (
                <Link to={item.url} className={item.cName} key={index}>
                  <li>
                    {item.title}
                  </li>
                </Link>
              )
            })}
          </ul>
        </nav>
    } else if (pathURL === '/MoslemApps/Tafsir/surah/:id') {
      nav =
        <nav className="NavbarItems">
          <h1 className="navbar-logo">
            <Link to="/MoslemApps/Tafsir">
              <i className="fas fa-arrow-circle-left"></i>
            </Link>
            Tafsir
            <i className="fas fa-book-reader"></i>
          </h1>
          <div className="menu-icon" onClick={this.handleClickMenu}>
            <i className={this.state.clicked ? 'fas fa-times' : 'fas fa-bars'}></i>
          </div>
          <ul className={this.state.clicked ? 'nav-menu active' : 'nav-menu'}>
            {MenuItem.map((item, index) => {
              return (
                <Link to={item.url} className={item.cName} key={index}>
                  <li>
                    {item.title}
                  </li>
                </Link>
              )
            })}
          </ul>
        </nav>
    } else if (pathURL === '/MoslemApps/Murottal') {
      nav =
        <nav className="NavbarItems">
          <h1 className="navbar-logo">
            Murottal
            <i className="fas fa-play"></i>
          </h1>
          <div className="menu-icon" onClick={this.handleClickMenu}>
            <i className={this.state.clicked ? 'fas fa-times' : 'fas fa-bars'}></i>
          </div>
          <ul className={this.state.clicked ? 'nav-menu active' : 'nav-menu'}>
            {MenuItem.map((item, index) => {
              return (
                <Link to={item.url} className={item.cName} key={index}>
                  <li>
                    {item.title}
                  </li>
                </Link>
              )
            })}
          </ul>
        </nav>
    } else if (pathURL === '/MoslemApps/Murottal/surah/:id') {
      nav =
        <nav className="NavbarItems">
          <h1 className="navbar-logo">
            <Link to="/MoslemApps/Murottal">
              <i className="fas fa-arrow-circle-left"></i>
            </Link>
            Murottal
            <i className="fas fa-play"></i>
          </h1>
          <div className="menu-icon" onClick={this.handleClickMenu}>
            <i className={this.state.clicked ? 'fas fa-times' : 'fas fa-bars'}></i>
          </div>
          <ul className={this.state.clicked ? 'nav-menu active' : 'nav-menu'}>
            {MenuItem.map((item, index) => {
              return (
                <Link to={item.url} className={item.cName} key={index}>
                  <li>
                    {item.title}
                  </li>
                </Link>
              )
            })}
          </ul>
        </nav>
    }
    return (
      <div>
        {nav}
      </div>
    )
  }
}

export default Navbar;